<template>
  <div id="recommend-table">
    <h2></h2>
    <div class="input-group col-sm-12" role="group"
         aria-label="Difficulty Table">
      <div class="input-group-prepend">
        <label for="table" class="btn btn-info text-nowrap"
               v-tooltip="''">
          
          <font-awesome-icon :icon="['fas', 'question-circle']"/>
        </label>
      </div>
      <select id="table" class="form-control"
              name="table" v-model="selected">
        <option v-for="(name,index) in list" :key="index">
          {{ name }}
        </option>
      </select>
    </div>
    URLbeatoraja<br/>
    <div class="form-group row align-items-center">
      <div class="btn-group col-sm-12">
        <label for="url_box"></label>
        <input id="url_box" class="form-control" type="text" :value="url"
               disabled>
        <button class="btn btn-secondary" @click="doCopy">
          <font-awesome-icon :icon="['fas', 'copy']"/>
        </button>
      </div>
    </div>
  </div>
</template>

<script>
import Api from "../api";
import * as log from 'loglevel'

export default {
  name: "RecommendTable",
  props: {
    user_id: {
      type: Number,
      required: true
    }
  },
  data: () => ({
    tables: null,
    selected: "",
  }),
  methods: {
    doCopy() {
      this.$copyText(this.url).then(function (e) {
        log.debug(e)
      }, function (e) {
        log.debug(e)
      })
    }
  },
  mounted() {
    Api.fetch_tables(this.$store.getters.accessToken).then(
        t => {
          this.tables = t
          this.selected = t.first()
        }
    )
  },
  computed: {
    list() {
      if (this.tables) {
        return this.tables.name_list()
      } else {
        return []
      }
    },
    /**
     * @returns {string}
     */
    url() {
      if (this.tables && this.tables.table_index(this.selected) >= 0) {
        return Api.get_table_header_url(this.user_id, this.tables.table_index(this.selected))
      } else {
        return ""
      }
    }
  },
}
</script>

<style scoped>
#recommend-table {
  padding-top: 20px;
  max-width: 800px;
  margin: 0 auto 0 auto;
}

</style>